* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: DM Sans;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.icon {
  fill: currentColor;
  stroke: currentColor;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.circle-btn {
  height: 18px;
  width: 18px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  transition: background-color .3s;
}

.circle-btn--products {
  height: 40px;
  width: 40px;
  background-color: #fff;
}

.circle-btn__arrow {
  stroke: #d41443;
  stroke-width: 4px;
}

.circle-btn__arrow--products {
  text-align: center;
  margin: 0 auto;
  display: block;
}

.circle-btn:hover {
  background-color: #ecebeb;
}

.button {
  color: #fff;
  cursor: pointer;
  background-color: #d41443;
  border: none;
  border-radius: 2em;
  padding: .9em 1.7em;
  font-family: DM Sans;
  transition: background-color .3s;
}

.button:hover {
  background-color: #c7133e;
}

.button--reversed {
  color: #d41443;
  background-color: #fff;
}

.button--reversed:hover {
  background-color: #ecebeb;
}

.button--small {
  font-size: 8px;
}

.button--mid {
  font-size: 14px;
  font-weight: 700;
}

.button--big {
  font-size: 16px;
  font-weight: 700;
}

.button--menu {
  width: 100%;
  max-width: 210px;
  margin-bottom: 19px;
}

.button a {
  color: inherit;
  text-decoration: none;
}

.button__chevron {
  stroke: currentColor;
  vertical-align: middle;
  margin-left: 13px;
}

@media screen and (min-width: 768px) {
  .button--mid {
    padding: .85em 2.3em;
    font-size: 16px;
  }

  .button--menu {
    max-width: 186px;
  }
}

@media screen and (min-width: 1280px) {
  .button--small {
    padding: .85em 1.6em;
    font-size: 14px;
  }
}

.menu-toggle {
  cursor: pointer;
  background: none;
  border: none;
}

.ellipsis:after, .ellipsis:before, .ellipsis {
  height: 4px;
  width: 4px;
  border-radius: 50%;
}

.ellipsis {
  background-color: #fff;
  margin: 0 auto;
  position: relative;
}

.ellipsis:before {
  content: "";
  background-color: #fff;
  position: absolute;
  right: 10px;
}

.ellipsis:after {
  content: "";
  background-color: #fff;
  position: absolute;
  left: 10px;
}

.ellipsis--reversed, .ellipsis--reversed:after, .ellipsis--reversed:before {
  background-color: #c7133e;
}

.header {
  background-color: #ffb8ca;
}

.header__box {
  justify-content: space-between;
  align-items: center;
  padding-top: 6px;
  display: flex;
}

.header__box-btn {
  display: none;
}

.header .menu-toggler {
  border: none;
}

.header .menu-toggler__icon {
  stroke: #d41443;
  background-color: #ffb8ca;
  display: block;
}

.header .nav {
  display: none;
}

.menu-toggle {
  border: none;
}

.nav {
  display: none;
}

.hero {
  height: 450px;
  z-index: -2;
  background-color: #ffb8ca;
  position: relative;
  overflow: hidden;
}

.hero__container {
  height: 100%;
  position: relative;
}

.hero-bg {
  width: 346px;
  height: 344px;
  z-index: -1;
  background-image: url("circlebg.e14bdfc0.png");
  background-size: 100%;
  position: absolute;
  bottom: 75px;
  right: -62px;
}

.hero-bg__image {
  position: absolute;
  top: 10px;
  left: 50%;
