  text-transform: uppercase;
  background-color: #0000;
  margin-bottom: 20px;
  font-family: Titan One;
  font-size: 26px;
  font-weight: 400;
  line-height: 1.86rem;
}

.hero__title--boldred {
  color: #d41443;
  font-size: 48px;
  line-height: 2.87rem;
}

.hero___circle, .hero__box, .hero__number, .hero__number-descripion {
  display: none;
}

@media screen and (min-width: 768px) {
  .header__box-btn {
    display: flex;
  }

  .hero__box {
    display: flex;
    position: relative;
  }

  .hero__box-picture {
    position: absolute;
    bottom: 28px;
    left: 34px;
  }

  .hero__box-person {
    position: absolute;
    bottom: 0;
    right: 34px;
  }

  .hero__box-bottom {
    max-width: 114px;
    position: absolute;
    bottom: 59px;
    left: 154px;
  }

  .hero__box-text {
    color: #fff;
