@media screen and (min-width: 1280px) {
  .menu-toggle {
    display: none;
  }

  .nav {
    gap: 35px;
    list-style-type: none;
    display: flex;
  }

  .nav__link {
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.3rem;
    text-decoration: none;
  }

  .nav__link:focus, .nav__link:hover {
    color: #d41443;
  }
}

.menu-container {
  z-index: 999;
  text-align: center;
  width: 250px;
  height: 100%;
  opacity: 0;
  background-color: #e17992;
  padding-top: 60px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
  box-shadow: -8px 0 20px #00000040;
}

.menu-container.is-open {
  opacity: 1;
  transform: translateX(0);
}

.js-close-menu {
  color: #fff;
  position: absolute;
  top: 23px;
  right: 28px;
}

.js-close-menu:hover, .js-close-menu:focus {
  color: #d41443;
}

.mobile-nav__list {
  text-align: left;
  list-style: none;
}

.mobile-nav__item {
  border-top: 1px solid #e18298;
  padding: 12px 0;
}

.mobile-nav__item:last-child {
  border-bottom: 1px solid #e18298;
}

.mobile-nav__link {
  color: #fff;
  padding-left: 21px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
}

